import angular from "angular";

import { upgradeModule } from "@uirouter/angular-hybrid";
import uiRouter from "@uirouter/angularjs";
import { WSForm, WSUserControl } from "etc-assets/user-control-es";

import "./app.less";

import { idsAssetsModule } from "../../modules/ids-assets/module";
import { componentsModule } from "../appbuilder/components/module";
import { runtimeModule } from "../appbuilder/runtime/module";

import { AngularConstants } from "../appbuilder/common/angular-constants";
import { AppState } from "./app.state";

//#region App Hub Dashboard imports
import { AppHubDirective } from "./apphub/apphub";
import { HubService } from "./apphub/services/hub-service";

// Runtime App Imports
import { ComponentValueDirective } from "./component-value-directive";
//#endregion

//#region Administration imports
import { AdministrationDirective } from "./administration/administration";
import { FeatureCatalog } from "./administration/feature-catalog/feature-catalog";
import { SaveService } from "./administration/services/save-service";

// App Card imports
import { AdminAppCard } from "./administration/admin-app-card/admin-app-card";

// Side Panel imports
import { AddRolesDialog } from "./administration/admin-sidebar/add-roles-dialog/add-roles-dialog";
import { AdminSidebar } from "./administration/admin-sidebar/admin-sidebar";
import { UrlValueDialogCtrl } from "./administration/admin-sidebar/url-value-dialog/url-value-dialog";
import { VersionDialogCtrl } from "./administration/admin-sidebar/version-dialog/version-dialog";
import { AdministrationService } from "./administration/services/administration-service";

//#endregion

import { Debugger } from "../appbuilder/common/debugger";
import { DeveloperToolComponent } from "./dev/developer-tool-component";

if (JENKINS_JOB) {
	console.debug(process.env.NODE_ENV);

	// Specifically so Jenkins' acc-test job can find jQuery
	window.$ = jQuery;
}

export const runtimeApp = angular.module("app-hub", [
	uiRouter,
	upgradeModule.name,
	idsAssetsModule.name,
	componentsModule.name,
	runtimeModule.name
]);

runtimeApp.config([
	"$qProvider", "$compileProvider",
	($qProvider: ng.IQProvider, $compileProvider: ng.ICompileProvider) => {
		$qProvider.errorOnUnhandledRejections(false);
		$compileProvider.debugInfoEnabled(Debugger.isEnabled());
		$compileProvider.commentDirectivesEnabled(false);
		$compileProvider.cssClassDirectivesEnabled(false);
	}
]);

runtimeApp.run([
	AngularConstants.STATE_SERVICE,
	(
		$state: ng.ui.IStateService
	) => {
		WSUserControl.onReady(() => {
			parent.MG.App.doUnloadPrompt = false;
			WSForm.getVarValue("InitURL", (initUrl: string) => {
				const prefix = "run/";
				if (initUrl !== "" && initUrl.indexOf(prefix) === 0) {
					const id = initUrl.slice(prefix.length);
					$state.go(AppState.APP_LAUNCHER, {
						id
					});
				}
			});
		});
	}]);

AppState.add(runtimeApp);

//#region App Hub Dashboard
AppHubDirective.add(runtimeApp);
HubService.add(runtimeApp);

// Runtime Apps
ComponentValueDirective.add(runtimeApp);
//#endregion

//#region Administration
AdministrationDirective.add(runtimeApp);
FeatureCatalog.add(runtimeApp);
SaveService.add(runtimeApp);

// App Card
AdminAppCard.add(runtimeApp);

// Side Panel
AdministrationService.add(runtimeApp);
AdminSidebar.add(runtimeApp);
AddRolesDialog.add(runtimeApp);
VersionDialogCtrl.add(runtimeApp);
UrlValueDialogCtrl.add(runtimeApp);

//#endregion

// Services
// MingleService.add(runtimeApp);
// LanguageService.add(runtimeApp);

DeveloperToolComponent.add(runtimeApp);
