import { AngularConstants } from "../../../appbuilder/common/angular-constants";

import { AppStatus, Config, InjectNames, MGPlatform, ReplaceEnum, ServerPaths } from "ng2/common/constants/common.constant";
import { IJsonRuntimeMashup } from "ng2/common/model/databearer/json-mashup.model";
import { IProjectInfo, ProjectInfo } from "ng2/common/project/project-info";
import { Helper } from "../../../appbuilder/common/helper";
import { InstanceEvent } from "../../../appbuilder/common/instance-event";
import { IProjectRole, IRole } from "../../model/project-role";
import { IProjectSetting } from "../../model/project-settings";

import { HttpService } from "../../../appbuilder/common/services/http-service";
import { SaveService } from "./save-service";

export class AdministrationService {
	public projectSettingsChangedEvent = new InstanceEvent<IProjectInfo>();

	public static $inject = [
		AngularConstants.Q,
		AngularConstants.WINDOW,
		InjectNames.HTTP_SERVICE,
		InjectNames.SAVE_SERVICE,
	];

	constructor(
		private $q: ng.IQService,
		private $window: ng.IWindowService,
		private httpService: HttpService,
		private saveService: SaveService,
	) { }

	/**
	 * Get IFS group roles. It will only get group roles for a project if optional parameter is supplied
	 *
	 * @param projectId
	 */
	public getIfsRoles(): ng.IPromise<IRole[]> {
		return this.httpService.executeGet(ServerPaths.ADMINISTRATION + "ifsroles");
	}

	public getProjectRoles(projectId?: string): ng.IPromise<IProjectRole[]> {
		return this.httpService.executeGet(ServerPaths.ADMINISTRATION + "project/" + projectId + "/roles");
	}

	public loadRuntimeProjects(): ng.IPromise<IProjectInfo[]> {
		return this.httpService.executeGet(ServerPaths.ADMINISTRATION + "project");
	}

	public listProjectVersions(projectId: string): ng.IPromise<string[]> {
		return this.httpService.executeGet(ServerPaths.ADMINISTRATION + projectId + "/versions");
	}

	public exportProject(exportProjectId: string, exportFileName: string): void {
		const resourceName = ServerPaths.ADMINISTRATION + exportProjectId + "/export/";
		this.httpService.executeGet<{ base64: string, filename: string }>(
			resourceName, exportFileName)
			.then((exportResult: { base64: string, filename: string }) => {
				if (Config.MG_PLATFORM === MGPlatform.WEB) {
					const blob = Helper.convertBase64ToSaveableBlob(
						exportResult.base64, { type: "application/zip" });

					blob.saveAs(exportResult.filename);
				}
			});
	}

	public importProject(
		projectFilePath: string,
		replace: ReplaceEnum,
		appId?: string
	): ng.IPromise<IJsonRuntimeMashup> {
		const url = `${ServerPaths.ADMINISTRATION}import/${replace}/${appId ? appId : ""}`;
		const data = { filePath: projectFilePath };
		return this.httpService.executePost<IJsonRuntimeMashup>(url, data);
	}

	public saveProjectSetting(projectSettings: IProjectSetting, parts?: number, status?: number): ng.IPromise<any> {
		return this.saveService.saveCurrent(projectSettings, parts, status);
	}

	/**
	 * Activates a specific version of a project.
	 * @param projectId
	 * @param status
	 * @param version
	 */
	public activateProjectVersion(
		projectId: string, status: AppStatus, version: string
	): ng.IPromise<IProjectSetting> {
		return this.httpService.executeGet(ServerPaths.ADMINISTRATION + `${projectId}/activate/${status}`, {
			"version": version,
		});
	}

	public deleteRuntimeApp(projectId: string): ng.IPromise<void> {
		return this.httpService.executeDelete(ServerPaths.ADMINISTRATION + projectId);
	}

	public getRepositoryProject(projectId: string, selectedVersion: string): ng.IPromise<ProjectInfo> {
		return this.httpService.executeGet(ServerPaths.ADMINISTRATION + `${projectId}/${selectedVersion}`, {
			"version": selectedVersion,
		});
	}

	public static add(m: ng.IModule): void {
		m.service(InjectNames.ADMINISTRATION_SERVICE, AdministrationService);
	}
}
