import { AngularConstants } from "../../../../appbuilder/common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IJsonUrlVariable } from "ng2/common/model/databearer/json-url-variable.model";
import {
	IDatagridApi,
	IDataGridColumn,
	IDatagridColumnArgs,
	IDataGridOptions,
} from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { IDialogBaseScope } from "../../../../appbuilder/common/defines";
import { Helper } from "../../../../appbuilder/common/helper";
import { DialogButtonType, DialogService, IDialogResult } from "../../../../appbuilder/common/services/dialog-service";

export interface IDatagridUrlVariableItem extends IJsonUrlVariable {
	value?: string;
}

export class UrlValueDialogCtrl {
	public project: IJsonRuntimeManifest;
	public projectId: string;
	public urlVariables: IDatagridUrlVariableItem[];
	public urlValues: IDataPair[] = [];
	public urlName: string;
	public datagridOptions: IDataGridOptions<IDatagridUrlVariableItem>;
	private illegalCharactersRegex = /[!@#$%^&*(),.?=":{}|<>]/;

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.TIMEOUT,
		AngularConstants.ELEMENT,
		InjectNames.DIALOG_SERVICE
	];

	constructor(
		private $scope: IDialogBaseScope<IJsonRuntimeManifest>,
		private $timeout: ng.ITimeoutService,
		private $element: ng.IAugmentedJQuery,
		private dialogService: DialogService
	) {
		this.project = $scope.eirDialog.parameter;
		this.projectId = this.project.id;
		this.urlName = this.project.urlName;
		this.urlVariables = this.project.urlVariables;

		this.initDatagridOptions();
		this.$timeout(() => {
			this.initUrlVariablesValues();
		});
	}

	public updateUrlValue(updatedUrlValue: IDataPair): void {
		const urlVariable = this.urlValues.find((urlValue) => {
			return urlValue.name === updatedUrlValue.name;
		});

		if (urlVariable) {
			urlVariable.value = updatedUrlValue.value;
		} else {
			this.urlValues.push({
				name: updatedUrlValue.name,
				value: updatedUrlValue.value
			});
		}
	}

	public cancel(): void {
		this.$scope.eirDialog.close();
	}

	public save(): void {
		const hasIllegalCharacters = this.urlValues.find((urlValue) => {
			return this.illegalCharactersRegex.test(urlValue.value);
		});

		if (hasIllegalCharacters) {
			this.dialogService.showError("Error", "No special characters allowed.");
		} else {
			const result: IDialogResult<IDataPair[]> = {
				button: DialogButtonType.Ok,
				value: this.urlValues
			};
			this.$scope.eirDialog.close(result);
		}
	}

	private initUrlVariablesValues(): void {
		const myList = Helper.getListByRegex("\(SETVARVALUES((.*))\)", this.urlName);
		myList.forEach((urlData) => {
			const name = urlData.substring(0, urlData.indexOf("="));
			const value = urlData.substring(urlData.lastIndexOf("=") + 1);

			const datagridUrlVariable = this.urlVariables.find((urlVariable) => {
				return urlVariable.name === name;
			});
			if (datagridUrlVariable) {
				datagridUrlVariable.value = value;
			}
		});
	}

	private initDatagridOptions(): void {
		const columns: IDataGridColumn[] = [
			{
				id: "name",
				name: "Name",
				field: "name",
				resizable: true,
				sortable: true,
				formatter: Soho.Formatters.Ellipsis,
				width: "25%"
			},
			{
				id: "description",
				name: "Description",
				field: "description",
				resizable: true,
				sortable: true,
				formatter: Soho.Formatters.Ellipsis,
				width: "45%"
			},
			{
				id: "value",
				name: "Value",
				field: "value",
				resizable: false,
				sortable: false,
				editor: Soho.Editors.Input,
				isEditable: () => {
					return true;
				},
				formatter: Soho.Formatters.Ellipsis,
				width: "30%"
			},
		];

		this.datagridOptions = {
			columns: columns,
			isList: true,
			editable: true,
			selectable: SelectableEnum.Single,
			dataset: this.urlVariables
		};

		this.$element.find("#url-datagrid").on("cellchange",
			(e: JQueryEventObject, args: IDatagridColumnArgs<IDataPair>) => {
				const hasNewValue = args.value !== args.oldValue;
				const hasIllegalCharacters = this.illegalCharactersRegex.test(args.value);
				if (hasIllegalCharacters) {
					this.showCellError(args);
				}
				if (hasNewValue && args.oldValue !== undefined) {
					const updatedUrlValue = {
						name: args.rowData.name,
						value: args.value
					} as IDataPair;
					this.updateUrlValue(updatedUrlValue);
				}
			}
		);
	}

	private showCellError(args: IDatagridColumnArgs<IDataPair>): void {
		this.getSohoXiApi().showCellError(args.row, args.cell, "No special characters allowed.", "Error");
	}

	private getSohoXiApi(): IDatagridApi {
		return this.$element.find("#url-datagrid").data("datagrid");
	}

	public static add(m: ng.IModule): void {
		m.controller("eirUrlValueDialogCtrl", UrlValueDialogCtrl);
	}
}
