import { AngularConstants } from "../../appbuilder/common/angular-constants";
import { AppState } from "../app.state";

import { AppStatus, Config, InjectNames, SaveProjectSettingEnum } from "ng2/common/constants/common.constant";
import { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import { Debugger } from "../../appbuilder/common/debugger";
import { IProjectSetting } from "../model/project-settings";

import { DialogService } from "../../appbuilder/common/services/dialog-service";
import { AdministrationService } from "../administration/services/administration-service";

export class DeveloperToolCtrl {
	public static $inject = [
		AngularConstants.Q,
		AngularConstants.STATE_SERVICE,
		InjectNames.DIALOG_SERVICE,
		InjectNames.ADMINISTRATION_SERVICE
	];

	get isDeveloper(): boolean {
		return Debugger.isEnabled() && Config.IS_DEV;
	}

	private acceptanceTestAppIdList: string[];
	private activatedAcceptanceTestAppList: { id: string; name: string; description: string; status: number }[];

	constructor(
		private $q: ng.IQService,
		private $state: ng.ui.IStateService,
		private dialogService: DialogService,
		private adminstrationService: AdministrationService
	) {}

	public $onInit(): void {
		if (this.isDeveloper) {
			console.debug("I am developer");
		} else {
			this.$state.go(AppState.HOME, {
				isFromAdmin: true,
			});
		}

		const filterAcceptanceTestItems = (
			list: { id: string; name: string; description: string; status: number }[]
		): { id: string; name: string; description: string; status: number }[] => {
			return list
				.filter(item => item.name.indexOf("ACCTEST") === 0);
		};

		this.adminstrationService
			.loadRuntimeProjects()
			.then(runtimeApps => {
				return runtimeApps.map(app => {
					return {
						id: app.manifest.id,
						name: app.manifest.displayName,
						description: app.manifest.description,
						status: (app.manifest as IJsonRuntimeManifest).status
					};
				});
			})
			.then(filterAcceptanceTestItems)
			.then(result => {
				this.acceptanceTestAppIdList = result.map(app => app.id);
				this.activatedAcceptanceTestAppList = result.filter(app => app.status === 1);
			});
	}

	public static resolve(): void {
		Debugger.initialize();
	}

	public deleteAllAcceptanceTestApps(): void {
		const amount = this.acceptanceTestAppIdList.length;
		const promises: ng.IPromise<void>[] = [];

		this.acceptanceTestAppIdList.forEach((projectId, index) => {
			promises.push(
				this.adminstrationService.deleteRuntimeApp(projectId).then(() =>
					this.dialogService.showToast({
						title: "Deleted",
						message: `Deleted ${projectId}, still ${amount -
							index - 1} of ${amount} to process`
					})
				)
			);
		});

		this.$q.all(promises)
			.then(() =>
				this.dialogService.showToast({
					title: "Result",
					message: "All acceptance test projects deleted"
				}))
			.finally(() => this.acceptanceTestAppIdList.length = 0);
	}

	public deactivateAllApps(): void {
		const amount = this.activatedAcceptanceTestAppList.length;
		const promises: ng.IPromise<void>[] = [];

		this.activatedAcceptanceTestAppList.forEach((app, index) => {
			const stuff: IProjectSetting = {
				manifest: {
					id: app.id,
					status: app.status,
					displayName: app.name,
					description: app.description
				}
			} as any;

			promises.push(
				this.adminstrationService
					.saveProjectSetting(stuff, SaveProjectSettingEnum.INFO, AppStatus.OFFLINE)
					.then(() =>
						this.dialogService.showToast({
							title: "Deactivated",
							message: `Deactivated ${app.name}, still ${amount -
								index - 1} of ${amount} to process`
						})
					)
			);
		});

		this.$q.all(promises)
			.then(() =>
				this.dialogService.showToast({
					title: "Result",
					message: "All acceptance test projects deactivated"
				}))
			.finally(() => this.activatedAcceptanceTestAppList.length = 0);
	}
}
