import { SelectableEnum } from "ng2/common/constants/sohoxi.constant";
import {
	IDatagridApi,
	IDataGridColumn,
	IDataGridOptions,
	IDataGridToolbarOptions,
} from "ng2/common/model/sohoxi/sohoxi-datagrid.model";

import { AngularConstants } from "../../../../appbuilder/common/angular-constants";
import { IDialogBaseScope } from "../../../../appbuilder/common/defines";
import { Helper } from "../../../../appbuilder/common/helper";
import { IDialog } from "../../../../appbuilder/common/services/dialog-service";
import { IRowItem } from "../../../../appbuilder/common/sohoxi/datagrid-helper";
import { IRole } from "../../../model/project-role";

export class AddRolesDialog {
	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.ELEMENT
	];

	public dialog: IDialog<{ dataset: IRole[] }>;
	public datagridOptions: IDataGridOptions<IRole>;

	private selectedRoles: IRole[] = [];
	private dataset: IRole[];
	private datagridApi: IDatagridApi;

	constructor(
		private scope: IDialogBaseScope<{ dataset: IRole[] }>,
		private $element: ng.IAugmentedJQuery
	) {
		this.dialog = scope.eirDialog;
		this.dataset = this.dialog.parameter.dataset;

		this.initDatagrid();
		if (Helper.isIE()) {
			this.refreshSohoIcons();
		}
	}

	public initDatagrid(): void {
		const columns: IDataGridColumn[] = [];
		columns.push({
			id: "selectionCheckbox",
			sortable: false,
			resizable: false,
			formatter: Soho.Formatters.SelectionCheckbox,
			align: "center"
		});
		columns.push({
			id: "groupId",
			name: "Name",
			field: "groupId",
			sortable: true,
			resizable: true,
			textOverflow: "ellipsis",
			width: 200
		});
		columns.push({
			id: "displayName",
			name: "Description",
			field: "displayName",
			sortable: true,
			resizable: true,
			textOverflow: "ellipsis",
			width: 400,
		});

		const toolbarOptions: IDataGridToolbarOptions = {
			title: " ", // needs to be here to align search field to right
			results: true,
			keywordFilter: true,
			actions: false,
			collapsibleFilter: true,
		};

		const datagridOptions: IDataGridOptions<IRole> = {
			columns: columns,
			dataset: this.dataset,
			selectable: SelectableEnum.Multiple,
			paging: true,
			pagesize: 10,
			toolbar: toolbarOptions,
			enableTooltips: true
		};
		this.datagridOptions = datagridOptions;

		this.initDatagridOnSelectEvent();
	}

	private initDatagridOnSelectEvent(): void {
		this.$element.find("#role-list").on("selected", (event, rowItems: IRowItem<IRole>[]) => {
			if (rowItems.length > 0) {
				this.selectedRoles = rowItems.map(rowObject => rowObject.data);
			} else {
				this.selectedRoles = [];
			}
		});
	}

	private refreshSohoIcons(): void {
		setTimeout(() => {
			this.$element.find("use").each((idx: number, elem: Element) => {
				elem.setAttribute("xlink:href", elem.getAttribute("xlink:href"));
			});
		});
	}

	public add(): void {
		this.dialog.close({
			value: this.selectedRoles
		});
	}

	public cancel(): void {
		this.dialog.close();
	}

	public onRemove(): void {
		if (!this.datagridApi) {
			this.datagridApi = this.$element.find("#role-list").data("datagrid");
		}
		this.datagridApi.removeSelected();
	}

	static add(m: ng.IModule): void {
		m.controller("eirAddRoles", AddRolesDialog);
	}
}
