import angular from "angular";
import { WSForm } from "etc-assets/user-control-es";

import { AngularConstants } from "../../appbuilder/common/angular-constants";
import { AppState } from "../app.state";

import { ABFormScriptMethod, AppStatus, Config, InjectNames, Numbers, ReplaceEnum, SortEnum, Subcodes } from "ng2/common/constants/common.constant";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonCompositeComponent } from "ng2/common/model/databearer/components/json-composite-component.model";
import { IJsonDatasource } from "ng2/common/model/databearer/dataservice/json-datasource.model";
import { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IDesignerMashup } from "ng2/common/model/project/mashup/designer-mashup.model";
import { IProjectInfo } from "ng2/common/project/project-info";
import { Debugger } from "../../appbuilder/common/debugger";
import { IErrorResponse } from "../../appbuilder/common/error-response";
import { ISearchFieldOptions } from "../../appbuilder/common/model/sohoxi/sohoxi-searchfield.model";
import { ConflictResolutonService } from "../../appbuilder/common/services/conflict-resolution-service";
import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult, IMessageDialogOptions,
	IToastOptions, StandardDialogButtons
} from "../../appbuilder/common/services/dialog-service";
import { FeatureSwitchService } from "../../appbuilder/common/services/feature-switch-service";
import { TagService } from "../../appbuilder/common/services/tag-service";
import { FeatureSwitchCtrl } from "../../appbuilder/common/shared-components/base/feature-switch-ctrl";
import { IProjectRole } from "../model/project-role";
import { IProjectSetting } from "../model/project-settings";
import { AdministrationService } from "./services/administration-service";

export interface IJsonAppManifest extends IJsonRuntimeManifest {
	imported: boolean;
}

export class AdministrationCtrl extends FeatureSwitchCtrl {
	public filterEnum = SortEnum;
	private unsubscribers: Function[] = [];
	public apps: IProjectInfo[];
	public searchString: string;
	public selectedProjectSetting: IProjectSetting;

	public filterTypes: IDataPair[] = [
		{ name: "All Apps", value: undefined },
		{ name: "Active Apps", value: { manifest: { status: 1 } } },
		{ name: "Deactivated Apps", value: { manifest: { status: 0 } } },
		{ name: "Apps Imported by me", value: { manifest: { createdBy: Config.USER.name } } }
	];

	public sortPropertiesTypes: IDataPair[] = [
		{ name: "Import Date", value: "manifest.createdTime" },
		{ name: "Name", value: "manifest.displayName" }
	];

	public searchfieldOptions: ISearchFieldOptions = {
		collapsible: true,
		clearable: true,
	};

	public get isDeveloper(): boolean {
		return Debugger.isEnabled() && Config.IS_DEV;
	}

	public static $inject = [
		AngularConstants.TIMEOUT,
		AngularConstants.STATE_SERVICE,
		AngularConstants.ELEMENT,
		InjectNames.DIALOG_SERVICE,
		InjectNames.ADMINISTRATION_SERVICE,
		InjectNames.CONFLICT_RESOLUTION_SERVICE,
		InjectNames.TAG_SERVICE,
		InjectNames.FEATURE_SWITCH_SERVICE,
	];

	constructor(
		private $timeout: ng.ITimeoutService,
		private $state: ng.ui.IStateService,
		private $element: ng.IAugmentedJQuery,
		private dialogService: DialogService,
		private administrationService: AdministrationService,
		private conflictResolutionService: ConflictResolutonService,
		private tagService: TagService,
		featureService: FeatureSwitchService,
	) {
		super(featureService);

		this.loadRuntimeProjects();
		this.unsubscribers.push(administrationService.projectSettingsChangedEvent.on((project: IDesignerMashup) => {
			this.apps.forEach((app, index) => {
				if (project.manifest.id === app.manifest.id) {
					const newApps = angular.copy(this.apps);
					const newProjectInfo: IProjectInfo = {
						manifest: project.manifest,
						tags: project.tags ? project.tags : []
					};
					newApps[index] = newProjectInfo;
					this.apps = newApps; //Replace apps with new apps to trigger scope-change
					this.tagService.projectInfoChangedEvent.raise(this.apps);
				}
			});
		}));
	}

	public showSettingPanel(project: IProjectInfo): void {
		this.isSelectedProjectModified(() => {
			this.administrationService.getProjectRoles(project.manifest.id)
				.then((roles: IProjectRole[]) => {
					this.selectedProjectSetting = {
						manifest: (project.manifest as IJsonRuntimeManifest),
						roles: roles,
						tags: angular.copy(project.tags)
					};
				});
		});
	}

	public hideSettingPanel(): void {
		this.isSelectedProjectModified(() => {
			this.selectedProjectSetting = null;
		});
	}

	private isSelectedProjectModified(callback: Function): void {
		if (this.selectedProjectSetting && this.selectedProjectSetting.isModified) {
			this.dialogService
				.showYesNo(
					"Warning",
					"You have modified data. Do you want to discard it?",
					"warning-text")
				.then(result => {
					if (result.button === DialogButtonType.Yes) {
						callback();
					}
				});
		} else {
			callback();
		}
	}

	public importProject(): void {
		WSForm.call({
			name: ABFormScriptMethod.SHOW_OPEN_FILE_DIALOG,
			parameters: [
				"ABProject.iap", // Filename
				"Import App",  // Caption
				"AppBuilder App (*.iap)|*.iap" // Filter
			],
			onCompleted: (response) => response !== "-1" ?
				this.importProjectFile(response) : response
		});
	}

	private importProjectFile(filePath: string): void {
		this.administrationService.importProject(filePath, ReplaceEnum.DONOTHING)
			.then((importedProject) => {
				this.loadRuntimeProjects().then(() => {
					const toastOptions: IToastOptions = {
						title: "App Imported",
						message: "The App was successfully saved.",
						position: "top right",
						progressBar: true
					};
					this.dialogService.showToast(toastOptions);
					this.validateData(importedProject);
				});
			}).catch((errorResponse: IErrorResponse) => {
				this.handleImportAppError(filePath, errorResponse);
			});
	}

	private validateData(data: any): void {
		this.conflictResolutionService.checkIfInvalidImportedItems<IJsonDatasource>(
			"data service(s)",
			data.datasources,
		).catch((errorDialogOptions) => this.dialogService.showMessage(errorDialogOptions));
		this.conflictResolutionService.checkIfInvalidImportedItems<IJsonCompositeComponent>(
			"composite(s)",
			data.compositeComponents,
		).catch((errorDialogOptions) => this.dialogService.showMessage(errorDialogOptions));
	}

	private handleImportAppError(filePath: string, errorResponse: IErrorResponse): void {
		const dialogTitle = "Import failed";

		if (errorResponse.subcode === Subcodes.PROJECT_IMPORT_ERROR) {
			this.dialogService.showError(
				dialogTitle,
				errorResponse.message);
			Debugger.log(this, errorResponse.detail);
		} else {
			/* TODO: import-error-dialog.html
				Rename template and controller to import-conflict-dialog.
				Since this dialog handle import conflict rather than import error occured */
			const options: IDialogOptions<IErrorResponse> = {
				title: dialogTitle,
				template: require("../../appbuilder/common/dialogs/import-error/import-error-dialog.html"),
				parameter: errorResponse,
			};

			this.dialogService.show(options)
				.then((result: IDialogResult<any>) => {
					switch (result.button) {
						case DialogButtonType.Yes:
							if (errorResponse.subcode === Subcodes.PROJECT_DISPLAYNAME_CONFLICT) {
								return this.handleReplace(filePath, errorResponse.detail);
							}
							return this.handleReplace(filePath);
						default:
							return;
					}
				});
		}
	}

	private handleReplace(filePath: string, appId?: string): void {
		this.administrationService
			.importProject(filePath, ReplaceEnum.OVERWRITE, appId)
			.then((appProject) => {
				this.loadRuntimeProjects().then(() => {
					this.dialogService.showToast({
						title: "App Imported",
						message: "The App was successfully saved.",
						position: "top right",
						progressBar: true
					});
				}).finally(() => {
					this.apps.forEach((app) => {
						const appManifest = app.manifest as IJsonAppManifest;
						if (app.manifest.id === appProject.manifest.id) {
							appManifest.imported = true;
							this.$timeout(() => {
								appManifest.imported = false;
							}, Numbers.FIVE_THOUSAND);
						}
					});
				});
			}).catch((errorResponse) => {
				this.dialogService.showError("Import App Error", errorResponse.message);
			});
	}

	public openFeatureCatalog(): void {
		this.dialogService.showContextualActionPanel({
			title: "Feature Catalog",
			template: "<eir-feature-catalog></eir-feature-catalog>",
		});
	}

	public deleteProject(project: IJsonRuntimeManifest): void {
		if (project.status === AppStatus.ONLINE) {
			return;
		}
		const options: IMessageDialogOptions = {
			title: "Delete Project",
			message: "Please confirm that you want to delete this project.",
			standardButtons: StandardDialogButtons.YesNo,
		};
		this.dialogService.showMessage(options).then((result: IDialogResult<any>) => {
			if (result && result.button === DialogButtonType.Yes) {
				this.administrationService.deleteRuntimeApp(project.id).then(() => {
					this.selectedProjectSetting = null; //Clear sidebar
					this.apps.forEach((app, index) => {
						if (app.manifest.id === project.id) {
							this.apps.splice(index, 1);
						}
					});
				}).catch((errorResponse: IErrorResponse) => {
					const options: IMessageDialogOptions = {
						title: "Delete Project",
						message: errorResponse.message,
						standardButtons: StandardDialogButtons.Ok,
						isError: true,
					};
					this.dialogService.showMessage(options);
				});
			}
		});
	}

	private loadRuntimeProjects(): ng.IPromise<void> {
		return this.administrationService.loadRuntimeProjects().then((response: IProjectInfo[]) => {
			this.apps = response;
		}).catch((errorMessage: IErrorResponse) => {
			console.error("Unable to load projects");
		});
	}

	public navigateBack(): void {
		// remove toolbar before changing page.
		// soho issue
		const toolbar = this.$element.find(".toolbar");
		toolbar.remove();
		this.$state.go(AppState.HOME, {
			isFromAdmin: true,
		});
	}
}

export class AdministrationDirective {
	static add(m: ng.IModule): void {
		m.directive("eirAdministration", () => {
			return {
				controller: AdministrationCtrl,
				controllerAs: "ctrl",
				template: require("./administration.html")
			};
		});
	}
}
