import { AngularConstants } from "../../../../appbuilder/common/angular-constants";

import { InjectNames } from "ng2/common/constants/common.constant";
import { ProjectInfo } from "ng2/common/project/project-info";
import { IDialogBaseScope } from "../../../../appbuilder/common/defines";
import { DialogButtonType, IDialogResult } from "../../../../appbuilder/common/services/dialog-service";
import { AdministrationService } from "../../services/administration-service";

export interface IVersionDialogParameters {
	projectId: string;
	currentVersion: string;
}

export class VersionDialogCtrl {
	public versions: string[];
	public projectId: string;
	public currentVersion: string;
	public selectedVersion: string;

	static $inject = [
		AngularConstants.SCOPE,
		InjectNames.ADMINISTRATION_SERVICE,
	];

	constructor(
		private $scope: IDialogBaseScope<IVersionDialogParameters>,
		private adminService: AdministrationService
	) {
		this.projectId = $scope.eirDialog.parameter.projectId;
		this.currentVersion = $scope.eirDialog.parameter.currentVersion;

		this.adminService.listProjectVersions(this.projectId).then(versions => {
			this.versions = versions;
		}).catch(err => {
			console.error("Error getting project versions:", err);
		});
	}

	public toggleSelected(version: string): void {
		if (this.selectedVersion === version) {
			this.selectedVersion = null;
		} else {
			this.selectedVersion = version;
		}
	}

	public cancel(): void {
		const result: IDialogResult<string> = {
			value: null,
			button: DialogButtonType.Cancel,
		};
		this.$scope.eirDialog.close(result);
	}

	public switch(): void {
		this.adminService.getRepositoryProject(this.projectId, this.selectedVersion)
			.then(manifest => {
				const result: IDialogResult<ProjectInfo> = {
					value: manifest,
					button: DialogButtonType.Ok,
				};
				this.$scope.eirDialog.close(result);
			});
	}

	static add(m: ng.IModule): void {
		m.controller("eirVersionDialogCtrl", VersionDialogCtrl);
	}
}
