// MUPS-2682: Look into if this directive can be unified with the one used in App Builder
import { InjectNames } from "ng2/common/constants/common.constant";
import { FrameworkService } from "../appbuilder/components/services/framework-service";

export interface IComponentValueScope extends ng.IScope {
	content: any;
}

export class ComponentValueDirective implements ng.IDirective {

	private DEFAULT_VALUE: string  = "<Empty>";

	constructor(private frameworkService: FrameworkService) { }

	scope = {
		content: "=content"
	};

	template = "{{content}}";

	link = (
		scope: IComponentValueScope,
		element: ng.IAugmentedJQuery,
		attribute: ng.IAttributes
	) => {
		scope.$watch(
			() => {
				return this.frameworkService.isRuntimeMode();
			},
			(isRuntimeMode: boolean) => {
				if (!scope.content) {
					if (isRuntimeMode) {
						scope.content = "";
					} else {
						scope.content = this.DEFAULT_VALUE;
					}
				} else {
					scope.content = scope.content;
				}
			}
		);
	}

	static add(m: ng.IModule): void {
		const directive: ng.IDirectiveFactory = (frameworkService: FrameworkService) =>
			new ComponentValueDirective(frameworkService);
		directive.$inject = [InjectNames.FRAMEWORK_SERVICE];

		m.directive("eirComponentValue", directive);
	}
}
