import { AngularConstants } from "../../appbuilder/common/angular-constants";

import { AppTypeEnum, Config, InjectNames } from "ng2/common/constants/common.constant";
import { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IErrorResponse } from "../../appbuilder/common/error-response";
import { Helper } from "../../appbuilder/common/helper";
import { IMingleMessageData } from "../../appbuilder/common/model/mingle.model";
import { IApplicationMenuApi } from "../../appbuilder/common/model/sohoxi/sohoxi-application-menu.model";
import { ITab, ITabContent } from "../../appbuilder/common/model/sohoxi/sohoxi-tabs.model";

import { CommonService } from "../../appbuilder/common/services/common-service";
import { LanguageService } from "../../appbuilder/common/services/language-service";
import { MingleService } from "../../appbuilder/common/services/mingle-service";
import { HubService } from "./services/hub-service";

export class ApphubCtrl {

	public get isAdmin(): boolean {
		const user = Config.USER;
		return user && user.roles.administrator;
	}

	public manifests: IJsonRuntimeManifest[];
	public tabsAPI: ITab;

	public tabList: string[] = [];

	static $inject = [
		AngularConstants.SCOPE,
		AngularConstants.TIMEOUT,
		AngularConstants.ELEMENT,
		AngularConstants.COMPILE,
		AngularConstants.STATE_PARAMS,
		InjectNames.COMMON_SERVICE,
		InjectNames.MINGLE_SERVICE,
		InjectNames.LANGUAGE_SERVICE,
		InjectNames.HUB_SERVICE
	];

	constructor(
		private $scope: ng.IScope,
		private $timeout: ng.ITimeoutService,
		private $element: ng.IAugmentedJQuery,
		private $compile: ng.ICompileService,
		private $stateParams: ng.ui.IStateParamsService,
		private commonService: CommonService,
		private mingleService: MingleService,
		private languageService: LanguageService,
		private hubService: HubService
	) {

		this.$timeout(() => {
			$("body").initialize("en-US");
			this.tabsAPI = $("#apphub-tabs").data("tabs");
			this.setTabActivatedBehavior();
			this.setTabCloseBehavior();
			this.setOpenTabBehaviour();
			this.languageService.detectLanguage();

			//console.info(this.languageService.getLanguage());
			//MUPS-2239
			//this.setupMingleDrillbacks();
			//this.setupMingleFavoritesContext();
			//this.mingleService.sendApplicationInitComplete();
		});

		this.commonService.loadActivatedApps().then((response: IJsonRuntimeManifest[]) => {
			this.manifests = response;
			this.openTabs();
		}, (errorMessage: IErrorResponse) => {
			console.error(errorMessage + ": Unable to load projects");
		});
	}

	private setTabActivatedBehavior(): void {
		$("#apphub-tabs").on("activated.tabs", (event: JQueryEventObject, object: JQuery) => {
			const activatedTab = object[0];
			//const name = activatedTab.innerHTML;
			const id = activatedTab.getAttribute("href").replace("#", "");
			this.hubService.setSelectedTabById(id);
			//this.mingleService.sendBusinessContextMessage(MingleService.MESSAGETYPE_BUSINESSCONTEXT, id, name); MUPS-2239
			//this.mingleService.sendBusinessContextMessage(MingleService.MESSAGETYPE_SHORTCUTCONTEXT, id, name); MUPS-2239
		});
	}

	private setTabCloseBehavior(): void {
		$("#apphub-tabs").on("close", (event: JQueryEventObject, object: JQuery) => {
			const removedTabId: string = object.find("a").attr("href");
			const index = this.tabList.indexOf(removedTabId.substr(1, removedTabId.length));
			if (index !== -1) {
				this.tabList.splice(index, 1);
				this.setOpenedTabs();
			}

			if (this.tabList.length === 0 && removedTabId !== "#start") {
				this.createStartTab();
				this.hubService.setSelectedTabById(null);
				//this.mingleService.sendClearContextMessage(); MUPS-2239
			}
		});
	}

	private setOpenTabBehaviour(): void {
		this.$element.on("applicationmenuopen", () => {
			this.createTabs();
		});
	}

	public openHelp(): void {
		this.commonService.openHelp();
	}

	public setupMingleDrillbacks(): void {
		this.mingleService.setupMessageHandler(
			MingleService.MESSAGETYPE_APPLICATIONDRILLBACK, (data: IMingleMessageData) => {
				const params = Helper.parseQueryString(data.applicationDrillback);
				const appId = params.$appId;

				if (appId) {
					const manifest = this.getManifest(appId);
					if (manifest) {
						this.addTab(manifest.displayName, manifest.id);
					}
				}
			});
	}

	public setupMingleFavoritesContext(): void {
		this.mingleService.setupMessageHandler(
			MingleService.MESSAGETYPE_FAVORITECONTEXT, (data: IMingleMessageData) => {
				const params = Helper.parseQueryString(data.favoriteContext);
				const appId = params.$appId;

				if (appId) {
					const manifest = this.getManifest(appId);
					if (manifest) {
						this.addTab(manifest.displayName, manifest.id);
					}
				}
			});
	}

	private createTabs(): void {
		const extraTimeout = 100;
		this.$timeout(() => {
			const tabIds = this.hubService.getOpenedTabIds();
			if (tabIds.length > 0) {
				const focusedId = this.hubService.getSelectedTabId();
				tabIds.forEach((id: string, index: number) => {
					const manifest = this.getManifest(id);
					if (manifest) {
						this.tabList.push(manifest.id);
						const tabContent = this.createTabContent(manifest.id, manifest.displayName);
						this.tabsAPI.add(manifest.id, tabContent);
					}
					if (tabIds.length === index + 1) {
						this.setTabFocused(focusedId);
					}
				});
			} else {
				this.createStartTab();
			}
		}, extraTimeout);
	}

	private getManifest(appId: string): IJsonRuntimeManifest {
		return this.manifests.find(manifest => manifest.id === appId);
	}

	private openTabs(): void {
		if (this.$stateParams.isFromAdmin) {
			const noFocus = false;
			this.getSoHoXiAPI().openMenu(noFocus);
		} else {
			this.createTabs();
		}
	}

	private getSoHoXiAPI(): IApplicationMenuApi {
		return this.$element.find("#application-menu").data("applicationmenu");
	}

	private createStartTab(): void {
		const tabContent: ITabContent = {
			name: " ",
			content: `<div class='start-page'>
						<div class='apphubSplash'>
							<p class='apphubTitle'>Infor App Hub</p>
						</div>
					</div>
					<eir-licenses-button
						app-name="Infor App Hub"
					></eir-licenses-button>
					`
		};
		this.tabsAPI.add("start", tabContent, 0);
		this.$compile("#module-tabs-container")(this.$scope);
		$("li:has(a[href$='start'])").addClass("inactiveTab");
	}

	public addTab(displayName: string, id: string): void {
		if (!this.isDuplicateTab(id)) {
			this.tabList.push(id);
			this.tabsAPI.add(id, this.createTabContent(id, displayName));
			this.setOpenedTabs();
		}

		this.setTabFocused(id);
		if (Helper.isMobile()) {
			// closes the sidebar
			this.tabsAPI.tablist.trigger("close-applicationmenu");
		}

		this.removeStartTab();
	}

	private removeStartTab(): void {
		this.tabsAPI.remove("start");
	}

	private setOpenedTabs(): void {
		this.hubService.setOpenedTabIds(this.tabList);
	}

	private setTabFocused(id: string): void {
		const tabToFocus = this.tabsAPI.tablist.find(":has(a[href$='" + id + "'])");
		tabToFocus.trigger("mousedown");
	}

	private isDuplicateTab(tabContent: string): boolean {
		return this.tabList.indexOf(tabContent) >= 0;
	}

	private createTabContent(projectId: string, name: string): ITabContent {
		const tabHtmlContent = `
			<div class='project-frame'>
				<eir-app-launcher app-type="${AppTypeEnum.APPHUB}" project-id="${projectId}" />
			</div>`;

		const result = this.$compile(tabHtmlContent)(this.$scope);

		const tabContent: ITabContent = {
			name: name,
			content: result,
			isDismissible: true
		};
		return tabContent;

	}
}

export class AppHubDirective {
	static add(m: ng.IModule): void {
		m.directive("eirApphub", () => {
			return {
				controller: ApphubCtrl,
				controllerAs: "ctrl",
				template: require("./apphub.html"),
			};
		});
	}
}
