import { WSForm } from "etc-assets/user-control-es";

import { ABFormScriptMethod, AppStatus, Config, InjectNames, MGPlatform, SaveProjectSettingEnum } from "ng2/common/constants/common.constant";
import { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IJsonTag } from "ng2/common/model/databearer/json-tag.model";
import { IExportDialogParams } from "../../../appbuilder/common/dialogs/export/export-dialog";
import { Helper } from "../../../appbuilder/common/helper";
import { DialogButtonType, DialogService, IDialogOptions, IDialogResult, IToastOptions } from "../../../appbuilder/common/services/dialog-service";
import { IProjectSetting } from "../../model/project-settings";

import { AdministrationService } from "../services/administration-service";

export class AdminAppCard {
	public projectSetting: IProjectSetting;
	public manifest: IJsonRuntimeManifest;
	public tags: IJsonTag[];

	static $inject: string[] = [
		InjectNames.DIALOG_SERVICE,
		InjectNames.ADMINISTRATION_SERVICE,
	];

	constructor(
		private dialogService: DialogService,
		private administrationService: AdministrationService
	) {
	}

	public get ProjectImportedBy(): string {
		return Helper.htmlEncode(this.manifest.createdByDisplayName);
	}

	public get ProjectVersion(): string {
		return Helper.htmlEncode(this.manifest.version);
	}

	public exportProject(): void {
		const projectId = this.manifest.id;

		if (Config.MG_PLATFORM === MGPlatform.WEB) {
			const displayName = this.manifest.displayName;
			const options: IDialogOptions<IExportDialogParams> = {
				title: "Export Project",
				template: require("../../../appbuilder/common/dialogs/export/export-dialog.html"),
				parameter: {
					type: "project",
					filename: displayName,
				}
			};
			this.dialogService.show(options).then((result: IDialogResult<string>) => {
				if (result.button === DialogButtonType.Ok) {
					this.administrationService.exportProject(projectId, result.value);
				}
			});
		} else { // MG_PLATFORM === WinStudio
			WSForm.call({
				name: ABFormScriptMethod.SHOW_SAVE_FILE_DIALOG,
				parameters: [
					`${this.manifest.displayName}.iap`, // Filename
					"Export Filename",  // Caption
					"AppBuilder Project (*.iap)|*.iap" // Filter
				],
				onCompleted: filePath => {
					this.administrationService.exportProject(projectId, filePath);
				}
			});
		}
	}

	public toggleActivated(): void {
		const project = this.manifest;
		const projectStatus = project.status ? AppStatus.OFFLINE : AppStatus.ONLINE;
		const statusText = projectStatus ? "Activated" : "Deactivated";

		this.administrationService.saveProjectSetting(
			this.projectSetting,
			SaveProjectSettingEnum.INFO,
			projectStatus
		).then(() => {
			project.status = projectStatus;
			const options: IToastOptions = {
				title: "App " + statusText,
				message: "The app successfully " + statusText,
				position: "top right",
				progressBar: true
			};
			this.dialogService.showToast(options);
			this.projectSetting.manifest.status = projectStatus;
		}).catch(() => {
			const options: IToastOptions = {
				title: "App",
				message: "The app unsuccessfully " + statusText,
				position: "top right",
				progressBar: true
			};
			this.dialogService.showToast(options);
		});
	}

	static add(m: ng.IModule): void {
		m.component("eirAppCard", {
			bindings: {
				projectSetting: "=",
				manifest: "<",
				tags: "<",
				onDelete: "&"
			},
			controller: AdminAppCard,
			controllerAs: "ctrl",
			template: require("./admin-app-card.html"),
		});
	}
}
