import { InjectNames } from "ng2/common/constants/common.constant";
import { FeatureSwitchService, IFeature } from "../../../appbuilder/common/services/feature-switch-service";

export class FeatureCatalog {
	public features: IFeature[];

	static $inject = [
		InjectNames.FEATURE_SWITCH_SERVICE,
	];

	constructor(private featureService: FeatureSwitchService) { }

	public $onInit(): void {
		this.features = this.featureService.getFeatures();
	}

	public toggleEnabled(feature: IFeature): void {
		this.featureService.toggleFeatureStatus(feature.id);
	}

	static add(m: ng.IModule): void {
		m.component("eirFeatureCatalog", {
			controller: FeatureCatalog,
			controllerAs: "ctrl",
			template: require("./feature-catalog.html"),
		});
	}
}
