import { InjectNames, SaveProjectSettingEnum, ServerPaths } from "ng2/common/constants/common.constant";
import { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import { AngularConstants } from "../../../appbuilder/common/angular-constants";
import { Helper } from "../../../appbuilder/common/helper";
import { InstanceEvent } from "../../../appbuilder/common/instance-event";
import { HttpService } from "../../../appbuilder/common/services/http-service";
import { IAppSetting, IProjectSetting } from "../../model/project-settings";

/* tslint:disable:no-bitwise */
export class SaveService {

	public saveStates = {
		inProgress: "Saving...",
		saved: "Last saved at ",
		failed: "Failed to save"
	};
	public saveStateEvent = new InstanceEvent<string>();

	public static $inject = [
		AngularConstants.Q,
		InjectNames.HTTP_SERVICE,
	];

	public constructor(
		private $q: ng.IQService,
		private httpService: HttpService,
	) {
	}

	private raiseSaveStateEvent(value: string): void {
		this.saveStateEvent.raise(value);
	}

	public saveCurrent(projectSettings: IProjectSetting, parts: number, status?: number): ng.IPromise<any> {
		return this.save(projectSettings, parts, status);
	}

	public save(projectSettings: IProjectSetting, flags: number, status?: number): ng.IPromise<any> {
		if (flags === SaveProjectSettingEnum.INFO) {
			return this.saveInfo(projectSettings, status);
		} else if (flags === SaveProjectSettingEnum.ROLES) {
			return this.saveRoles(projectSettings);
		} else { //ALL
			return this.saveInfo(projectSettings, status).then(() => {
				return this.saveTags(projectSettings).then(() => {
					return this.saveRoles(projectSettings);
				});
			});
		}
	}

	private saveInfo(projectSettings: IProjectSetting, status?: number): ng.IPromise<any> {
		const appSettings: IAppSetting = {
			displayName: projectSettings.manifest.displayName,
			description: projectSettings.manifest.description,
			status: status !== undefined ? status : projectSettings.manifest.status,
			urlName: projectSettings.manifest.urlName
		};
		return this.httpService.executePut(
			ServerPaths.ADMINISTRATION + "project/" + projectSettings.manifest.id + "/info", appSettings);
	}

	private saveRoles(projectSettings: IProjectSetting): ng.IPromise<any> {
		return this.httpService.executePut(
			ServerPaths.ADMINISTRATION + "project/" + projectSettings.manifest.id + "/roles", projectSettings.roles);
	}

	private saveTags(projectSettings: IProjectSetting): ng.IPromise<IJsonRuntimeManifest> {
		const tags = Helper.getListNames(projectSettings.tags);
		const url = "tags/" + ServerPaths.PROJECT + projectSettings.manifest.id + "?tagName=" + tags;
		return this.httpService.executePost<IJsonRuntimeManifest>(url);
	}

	public static add(ngModule: ng.IModule): void {
		ngModule.service(InjectNames.SAVE_SERVICE, SaveService);
	}
}
