import { InjectNames } from "ng2/common/constants/common.constant";

export class HubService {
	private openTabIds: string[] = [];
	private selectedTabId: string;

	public setOpenedTabIds(tabIds: string[]): void {
		this.openTabIds = tabIds;
	}

	public getOpenedTabIds(): string[] {
		return this.openTabIds;
	}

	public setSelectedTabById(tabId: string): void {
		this.selectedTabId = tabId;
	}

	public getSelectedTabId(): string {
		return this.selectedTabId;
	}

	public static add(m: ng.IModule): void {
		m.service(InjectNames.HUB_SERVICE, HubService);
	}
}
