import { AppTypeEnum } from "ng2/common/constants/common.constant";
import { AngularConstants } from "../appbuilder/common/angular-constants";

export class AppState {

	public static HOME: ng.ui.IState = {
		url: "/",
		name: "HOME",
		template: "<eir-apphub></eir-apphub>",
		params: {
			isFromAdmin: false,
		}
	};

	public static APP_LAUNCHER: ng.ui.IState = {
		url: "/run/" + ":id",
		name: "appLauncher",
		component: "eirAppLauncher",
		reloadOnSearch: false,
		resolve: {
			appType: () => AppTypeEnum.RUNTIME,
			projectId: [AngularConstants.STATE_PARAMS,
				($stateParams: ng.ui.IStateParamsService): string => $stateParams.id],
		}
	};

	public static ADMIN_APP_LAUNCHER: ng.ui.IState = {
		url: "/admin/run/" + ":id",
		name: "adminAppLauncher",
		component: "appLauncher",
		reloadOnSearch: false,
		resolve: {
			appType: () => AppTypeEnum.ADMIN_PREVIEW,
			projectId: [AngularConstants.STATE_PARAMS,
				($stateParams: ng.ui.IStateParamsService): string => $stateParams.id],
		}
	};

	public static ADMINISTRATION: ng.ui.IState = {
		url: "/admin",
		name: "administration",
		template: "<eir-administration></eir-administration>"
	};

	public static DEVELOPER: ng.ui.IState = {
		url: "/developer",
		name: "developer",
		template: "<eir-developer-tool></eir-developer-tool>",
	};

	public static add(module: ng.IModule): void {
		module.config([
			"$stateProvider",
			"$urlRouterProvider",
			(
				$stateProvider: ng.ui.IStateProvider,
				$urlRouterProvider: ng.ui.IUrlRouterProvider) => {

				$stateProvider
					.state(this.HOME)
					.state(this.APP_LAUNCHER)
					.state(this.ADMIN_APP_LAUNCHER)
					.state(this.ADMINISTRATION)
					.state(this.DEVELOPER);

				$urlRouterProvider.otherwise("/");
			}
		]);
	}
}
