import angular from "angular";
import { AngularConstants } from "../../../appbuilder/common/angular-constants";

import { InjectNames, SaveProjectSettingEnum } from "ng2/common/constants/common.constant";
import { IDataPair } from "ng2/common/model/data-pair.model";
import { IJsonRuntimeManifest } from "ng2/common/model/databearer/json-manifest.model";
import { IJsonTag } from "ng2/common/model/databearer/json-tag.model";
import { BackwardCompatible } from "ng2/common/project/backward-compatible";
import { ProjectInfo } from "ng2/common/project/project-info";

import { IListViewApi, IListViewOptions } from "../../../appbuilder/common/model/sohoxi/sohoxi-listview.model";
import { Utils } from "../../../appbuilder/common/sohoxi/utils";
import { IProjectRole, IRole } from "../../model/project-role";
import { IProjectSetting } from "../../model/project-settings";

import {
	DialogButtonType, DialogService, IDialogOptions, IDialogResult,
	IMessageDialogOptions, IToastOptions, StandardDialogButtons
} from "../../../appbuilder/common/services/dialog-service";
import { AdministrationService } from "../services/administration-service";
import { IVersionDialogParameters } from "./version-dialog/version-dialog";

export class AdminSidebar {

	public static URL_LIMIT = 50;
	public previousProjectSetting: IProjectSetting;
	public set projectSetting(value: IProjectSetting) {
		this.previousProjectSetting = angular.copy(this.innerProjectSetting = value);

		if (value) {
			this.roleListViewOptions = {
				dataset: value.roles,
				template: `<ul>
								{{#dataset}}
								<li>
										<p id="{{name}}" class="listview-heading">{{name}}</p>
								</li>
								{{/dataset}}
							</ul>`
			};

			this.innerProjectSetting.manifest = angular.copy(value.manifest);
			this.innerProjectSetting.isModified = value.isModified;
			this.innerProjectSetting.roles = angular.copy(value.roles);
			this.innerProjectSetting.tags = angular.copy(value.tags);

		}
	}
	public get projectSetting(): IProjectSetting {
		return this.innerProjectSetting;
	}
	private innerProjectSetting: IProjectSetting;

	public get project(): IJsonRuntimeManifest {
		return this.projectSetting ? this.projectSetting.manifest : null;
	}
	public get projectRoles(): IProjectRole[] {
		return this.projectSetting ? this.projectSetting.roles : null;
	}

	/**
	 * True if any (savable) setting has changed.
	 */
	public get isModified(): boolean {
		if (!this.projectSetting || !this.previousProjectSetting) {
			return false;
		}
		const newProject = this.projectSetting.manifest;
		const oldProject = this.previousProjectSetting.manifest;
		const newRoles = this.projectSetting.roles;
		const oldRoles = this.previousProjectSetting.roles;
		const newTags = this.projectSetting.tags;
		const oldTags = this.previousProjectSetting.tags;

		this.projectSetting.isModified = newProject.description !== oldProject.description ||
			newProject.displayName !== oldProject.displayName ||
			newProject.title !== oldProject.title ||
			newProject.urlName !== oldProject.urlName ||
			newProject.version !== oldProject.version ||
			this.deepListEquals(newRoles, oldRoles, "name") ||
			this.deepListEquals(newTags, oldTags, "name");

		return this.projectSetting.isModified;
	}

	/**
	 * TODO: Give better name, and move to somewhere else. Perhaps helper class
	 * deepListEquals compares if list of object has changed. Specically it checks for one property
	 * in objects if it has changed.
	 */
	private deepListEquals(listOfObj1: any[], listOfObj2: any[], propertyName: string): boolean {
		const result1 =
			listOfObj1.length !== listOfObj2.length ||
			listOfObj1.some(
				newRole => listOfObj2.findIndex(oldRole => oldRole[propertyName] === newRole[propertyName]) === -1);
		const result2 =
			listOfObj1.length === listOfObj2.length &&
			listOfObj1.some(
				newRole => listOfObj2.findIndex(oldRole => oldRole[propertyName] === newRole[propertyName]) === -1);
		return result1 || result2;
	}

	public disabledButtonSet: boolean = false;
	public ifsRoles: IRole[] = [];
	public roleListViewOptions: IListViewOptions;
	public appUrl: string;
	public limitUrl: number = AdminSidebar.URL_LIMIT;

	static $inject: string[] = [
		AngularConstants.ELEMENT,
		AngularConstants.Q,
		InjectNames.ADMINISTRATION_SERVICE,
		InjectNames.DIALOG_SERVICE,
	];

	constructor(
		private $element: ng.IAugmentedJQuery,
		private $q: ng.IQService,
		private administrationService: AdministrationService,
		private dialogService: DialogService,
	) {
		administrationService.getIfsRoles()
			.then(roles => this.ifsRoles = roles);
	}

	private previousProjectId: string;

	public $doCheck(): void {
		if (this.project && this.previousProjectId !== this.project.id) {
			this.limitUrl = AdminSidebar.URL_LIMIT;
			this.setSidebarBaseURL();
			this.previousProjectId = this.project.id;
		}
	}

	private setSidebarBaseURL(): void {
		const manifest = this.projectSetting.manifest;
		if (BackwardCompatible.tryUpdateUrlName(manifest)) { //backwards compatibility
			this.previousProjectSetting.manifest.urlName = manifest.urlName;

			const dontShowToast = true;
			this.save(dontShowToast);
		}

		this.appUrl = window.location.origin +
			"/WSWebClient/PlainForm.aspx?page=FormOnly&notitle=1&Form=AppHub" + this.projectSetting.manifest.urlName;
	}

	public showMore(): void {
		if (this.limitUrl === this.appUrl.length) {
			this.limitUrl = AdminSidebar.URL_LIMIT;
		} else {
			this.limitUrl = this.appUrl.length;
		}
	}

	public copyUrl(): void { //Copy url to clipboard
		const url = this.appUrl;
		const textArea = document.createElement("textarea");
		textArea.value = url;
		document.body.appendChild(textArea);
		textArea.select();

		try {
			document.execCommand("copy");
			const toastOptions: IToastOptions = {
				title: "Copy",
				message: "URL Copied to Clipboard",
				position: "top right"
			};
			this.dialogService.showToast(toastOptions);
		} catch (err) {
			console.warn("Unable to copy");
		}

		document.body.removeChild(textArea);
	}

	public saveTags(tags: IJsonTag[]): void {
		this.projectSetting.tags = tags;
	}

	public upgradeProject(): void {
		const options: IDialogOptions<IVersionDialogParameters> = {
			title: "Select App Version",
			template: require("./version-dialog/version-dialog.html"),
			style: "width: 500px; max-height: 500px;",
			parameter: {
				projectId: this.project.id,
				currentVersion: this.previousProjectSetting.manifest.version,
			},
		};
		this.dialogService.show<IVersionDialogParameters, ProjectInfo>(options).then(result => {
			if (result.button === DialogButtonType.Ok && result.value) {
				const manifest = result.value.manifest as IJsonRuntimeManifest;
				this.projectSetting.manifest = manifest;
				this.projectSetting.tags = result.value.tags;
				this.buildUrl(manifest);
			}
		}).catch(err => {
			console.error("Version dialog closed with error:", err);
		});
	}

	public editUrlValues(): void {
		if (this.projectSetting.manifest.urlVariables.length > 0) {
			const options: IDialogOptions<IJsonRuntimeManifest> = {
				title: "Edit URL Values",
				template: require("./url-value-dialog/url-value-dialog.html"),
				parameter: this.projectSetting.manifest,
				style: "width: 750px; height: 300px;"
			};
			this.dialogService.show(options).then((result: IDialogResult<IDataPair[]>) => {
				const button = result.button;
				if (button === DialogButtonType.Ok) {
					this.buildUrl(this.projectSetting.manifest, result.value);
				}
			});
		} else {
			const options: IMessageDialogOptions = {
				title: "Url Variables",
				message: "This App has no inbound Url Parameters.",
				standardButtons: StandardDialogButtons.Ok
			};
			this.dialogService.showMessage(options);
		}
	}

	public onAddRoles(): void {
		const options: IDialogOptions<{ dataset: IRole[] }> = {
			title: "Select Security Roles",
			template: require("./add-roles-dialog/add-roles-dialog.html"),
			parameter: {
				dataset: angular.copy(this.getFilteredAddedRoles())
			}
		};
		this.dialogService.show(options).then((result) => {
			if (result.value) {
				const ifsRoles = result.value as IRole[];
				ifsRoles.forEach(ifsRole => {
					this.projectSetting.roles.push({
						name: ifsRole.groupId
					});
				});
				this.roleListViewOptions.dataset = this.projectSetting.roles;
			}
		});
	}

	public onRemoveRole(): void {
		const api: IListViewApi = Utils.getData(this.$element.find("#roleList"), "listview");
		const selectedRole = api.getSelected();
		const indexToRemove = this.projectSetting.roles.findIndex((item) => {
			return item.name === selectedRole.find("p")[0].id;
		});

		if (indexToRemove !== -1) {
			api.remove(selectedRole as any);
			this.projectSetting.roles.splice(indexToRemove, 1);
			this.roleListViewOptions.dataset = this.projectSetting.roles;
		}
	}

	private buildUrl(manifest: IJsonRuntimeManifest, urlValues: IDataPair[] = []): void {
		var urlString = "";
		manifest.urlVariables.forEach((urlVariable) => {
			const variable = urlValues.find((urlValue) => {
				return urlValue.name === urlVariable.name;
			});
			urlString += `,${urlVariable.name}=`;
			if (variable) {
				urlString += variable.value;
			}
		});

		this.projectSetting.manifest.urlName = `(SETVARVALUES(InitURL=run/${manifest.id}${urlString})`;
		this.setSidebarBaseURL();
	}

	/**
	 * Filter out roles which is already added to project
	 */
	private getFilteredAddedRoles(): IRole[] {
		const filteredRoles: IRole[] = [];
		this.ifsRoles.forEach(ifsRole => {
			const result = this.projectRoles.find(projectRole => {
				return ifsRole.groupId === projectRole.name;
			});
			if (!result) {
				filteredRoles.push(ifsRole);
			}
		});
		return filteredRoles;
	}

	public discard(): void {
		if (this.disabledButtonSet) {
			return;
		}
		this.projectSetting = this.previousProjectSetting;
		this.roleListViewOptions.dataset = this.projectSetting.roles;
	}

	public save(noToast: boolean = false): void {
		if (this.disabledButtonSet) {
			return;
		}
		this.disabledButtonSet = true;
		let promise;
		if (this.previousProjectSetting.manifest.version !== this.projectSetting.manifest.version) {
			promise = this.doVersionUpgrade();
		} else {
			promise = this.administrationService
				.saveProjectSetting(this.projectSetting, SaveProjectSettingEnum.ALL);
		}
		promise
			.then((response: IProjectSetting) => {
				this.administrationService.projectSettingsChangedEvent.raise(response);

				this.projectSetting.tags = response.tags;
				this.previousProjectSetting = angular.copy(this.projectSetting);

				const options: IToastOptions = {
					title: "Properties Saved",
					message: "Changes to app properties have been saved",
					position: "top right",
					progressBar: true
				};

				if (!noToast) {
					this.dialogService.showToast(options);
				}

				this.disabledButtonSet = false;
			})
			.catch(errorResponse => {
				this.dialogService.showError("Save Error", errorResponse.message);
				this.disabledButtonSet = false;
			});
	}

	/**
	 * Check if version has changed, and performs an upgrade.
	 */
	private doVersionUpgrade(): ng.IPromise<IProjectSetting> {
		const newVersion = this.projectSetting.manifest.version;
		const projectId = this.projectSetting.manifest.id;
		const status = this.previousProjectSetting.manifest.status;

		return this.administrationService.activateProjectVersion(projectId, status, newVersion);
	}

	static add(m: ng.IModule): void {
		m.component("eirAdminSidebar", {
			bindings: {
				projectSetting: "=",
			},
			controller: AdminSidebar as any,
			controllerAs: "ctrl",
			template: require("./admin-sidebar.html"),
		});
	}
}
